<template>
  <div class="login">
    <div class="login-content">
      <p class="login-name">{{ APPNAME }}登录</p>
      <Form ref="form" :model="userInfo" :rules="rules" :label-width="70">
        <FormItem label="用户名:" prop="userName">
          <viInput
            v-model="userInfo.userName"
            placeholder="请输入用户名"
            @on-enter="login"
          ></viInput>
        </FormItem>
        <FormItem label="密码:" prop="password">
          <viInput
            v-model="userInfo.password"
            type="password"
            placeholder="请输入密码"
            @on-enter="login"
          ></viInput>
        </FormItem>
      </Form>
      <div class="login-tip">
        <Checkbox v-model="remember" @on-change="changeCheck('remember', $event)">记住密码</Checkbox>
        <Checkbox v-model="autoLogin" @on-change="changeCheck('autoLogin', $event)">自动登录</Checkbox>
      </div>
      <Button @click="login" long type="success">登录</Button>
    </div>
  </div>
</template>

<script>
import { Checkbox } from "view-ui-plus";
import { aesDecode, loginByPwd } from "@/utils";

export default {
  name: "login",
  components: { Checkbox },
  data() {
    return {
      APPNAME: window.APPNAME,
      userInfo: {},
      rules: {
        userName: { required: true, message: "请输入用户名" },
        password: { required: true, message: "请输入密码" },
      },
      remember: false,
      autoLogin: false,
    };
  },
  methods: {
    changeCheck(key, bool) {
      if (bool) {
        localStorage.setItem(key, "1");
      } else {
        localStorage.removeItem(key);
        if (key === "remember") {
          localStorage.removeItem("token");
        }
      }
    },
    login() {
      this.$refs.form.validate(async (valid) => {
        if (!valid) return this.$Message.error("数据校验不通过");
        try {
          await loginByPwd({ ...this.userInfo });
          if (this.$route.query.realName) {
            this.$router.replace({
              name: this.$route.query.realName,
              query: JSON.parse(this.$route.query.query),
              params: JSON.parse(this.$route.query.params),
            });
          } else if (window.isMobile) {
            this.$router.replace("/mobileHome");
          } else {
            this.$router.replace("/home");
          }
        } catch (err) {
          this.$Message.error(`登录失败：${err.message || err}`);
        }
      });
    },
  },
  mounted() {
    this.remember = localStorage.getItem("remember") === "1";
    this.autoLogin = localStorage.getItem("autoLogin") === "1";
    const token = sessionStorage.getItem("token") || localStorage.getItem("token");
    if (this.remember && token) {
      const [userName, password] = aesDecode(token)?.split("/");
      if (userName && password) {
        this.userInfo = { userName, password };
      }
    }
    if (this.autoLogin && token && !this.$route.query.from) {
      this.$nextTick(() => {
        this.login();
      });
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &-content {
    box-shadow: 0 0 5px #ccc;
    padding: 16px 20px 20px;
    width: 300px;
    border-radius: 4px;
    .login-name {
      font-size: 24px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 15px;
    }
    .login-tip {
      float: right;
      margin-bottom: 15px;
    }
  }
}
</style>
